.affairs__containerAll {
    margin: 20px;
}

.affairs__container {

    width: fit-content;
    margin: 0 auto;
    padding: 10px;
    border: 2px solid #48403b;
    border-radius: 20px;
}

.affair__element {
    margin: 10px;
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    gap: 60px;
}

.affair__btn {
    background-color: transparent;
    border: 2px solid #48403b;
    color: #48403b;
    border-radius: 20px;
    padding: 2px 5px;
}

.affair_text {
    align-self: center;

}

.affair__filterBtnsContainer {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    margin: 20px 20px 0px 20px;
}

.affair__filterBtns {
    background-color: transparent;
    border: 2px solid #48403b;
    color: #48403b;
    border-radius: 20px;
    padding: 2px 5px;
}

.affair__empty {
    margin-top: 10px;
    text-align: center;
}